.modal{
    background-color: #FFF;
    border-radius: 3px;
    box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
    display: none;
    padding: 40px !important;
    position: relative;
    width: 40%;
}

.modal__form{
    background: $form_background;
    border-radius: 3px;
    padding: 24px;
    .modal__heading{
        font-size: 20px;
        font-weight: 400;
        text-align: center;
    }
}

.modal__buttons-group .btn{
    width: 175px;
}